h1.title {
  font-size: 20pt;
  font-weight: bold;
  text-align: center;
  user-select: none;
}

h2.title {
  font-size: 15pt;
  font-weight: normal;
  text-align: center;
}

table.centered {
  margin-right: auto;
  margin-left: auto;
}

div.centered {
  text-align: center;
}

button.centered {
  background-color: lightgrey;
  border-radius: 50%;
  border: none;
  width: 20pt;
  height: 20pt;
  font-weight: bold;
  outline: none;
}

button.centered:active {
  background-color: whitesmoke;
}

textarea.no_resize {
  resize: none;
}

div.menu {
  display: inline-flex;
}
